import axios from "axios";

const URL = "https://namesgame.herokuapp.com/";        

//–æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç —Å–∫–∞–∑–∞–Ω–Ω–æ–µ –∏–≥—Ä–æ–∫–æ–º –∏–º—è
//–ø—Ä–æ–≤–µ—Ä—è–µ—Ç, –µ—Å—Ç—å –ª–∏ —ç—Ç–æ –∏–º—è –≤ –±–¥
//–µ—Å–ª–∏ –µ—Å—Ç—å, –≤ –æ—Ç–≤–µ—Ç –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç –∏–º—è, –∫–æ—Ç–æ—Ä–æ–µ –¥–æ–ª–∂–µ–Ω —Å–∫–∞–∑–∞—Ç—å –∞—Å—Å–∏—Å—Ç–µ–Ω—Ç
//–µ—Å–ª–∏ –Ω–µ—Ç, –æ—Ç–ø—Ä–∞–≤–∏—Ç –ø—É—Å—Ç—É—é —Å—Ç—Ä–æ–∫—É
export async function sendName(UserId, Name) {
    return await axios.post(`${URL}add_used`, {
        UserId,
        Name,
    });
}   

//–æ—á–∏—Å—Ç–∏—Ç—å —Å–ø–∏—Å–æ–∫ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–Ω—ã—Ö –∏–º–µ–Ω
export async function clearRequest(idUser) {
    await axios.post(`${URL}clear_user_info`, {"Id": idUser,});
}

//–µ—Å–ª–∏ –Ω–∏–∫–Ω–µ–π–º —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —Å —Ç–∞–∫–∏–º id –Ω–∞–π–¥–µ–Ω, —Ç–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –Ω–∏–∫, 
//–µ—Å–ª–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω –Ω–∏–∫ –∏–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å, –≤–µ—Ä–Ω–µ—Ç –ø—É—Å—Ç—É—é —Å—Ç—Ä–æ–∫—É
export async function findNickName(Id) {
    return await axios.post(`${URL}get_nickname`, { Id, });
}

//–ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –Ω–∏–∫–Ω–µ–π–º–∞ –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤ –±—ç–∫–µ–Ω–¥
export async function sendNickName(idUser, nickname) {
    await axios.post(`${URL}manage_nickname`, {
        "UserId": idUser,
        "Nickname": nickname,
    })
}   